import * as ko from "knockout";

// Contexts
import InputsBase, { IInputsBase } from "Content/TS/Shared/Controls/Inputs/inputs-base";

export default class InputEmail extends InputsBase {
    constructor(params: IInputsBase) {
        super(params);
    }
}

const inputEmailComponent = "input-email" as string;
ko.components.unregister(inputEmailComponent);
ko.components.register(inputEmailComponent, {
    viewModel: InputsBase,
    template: { require: `text!Content/TS/Shared/Controls/Inputs/Email/${inputEmailComponent}.html` }
});